				<li>"회사"는 서비스와 관련된 정보 및 광고를 사이트, 서비스 화면 등에 게재할 수 있습니다.</li>
				<li>"회사"는 서비스와 관련된 정보 및 광고를 전화, 전자우편, 휴대전화 문자메시지 등에 의하여 전송하려고 하는 경우에는 “이용자”의 사전 동의를 받습니다.</li>	
				<li>제2항에 따라 정보 및 광고를 제공받은 “이용자”는 원치 않을 경우 언제든지 가입신청 메뉴와 개인정보변경 메뉴 등에서 수신거부을 할 수 있습니다. 다만 관련법에 따른 거래관련정보 및 공시의무정보, 약관변경 안내 등은 수신거부에도 불구하고 전송될 수 있습니다.</li>						
			</ol>
		</div>
		<h2>제4장 서비스 이용</h2>
		<div>
			<h3>제12조 (광고의 게재 등)</h3>	
			<ol class="list-style03">
				<li>"회사"는 서비스에 적절하다고 판단되거나 활용 가능성 있는 광고를 사이트에 게재할 수 있습니다.</li>
				<li>"회사"는 사전 동의를 한 "이용자"에 한하여 "회사" 또는 제휴사의 상품소개 관련정보를 이메일, 휴대폰 SMS 등을 통해 발송할 수 있습니다.</li>
			</ol>
		</div>
		<div>
			<h3>제13조 (이용자의 게시물)</h3>	
			<ol class="list-style03">
				<li>"이용자"가 게시한 게시물의 내용에 대한 권리 및 책임은 "이용자"에게 있으며, "이용자"의 게시물이 타인의 지적재산권 등을 침해함으로써 발생하는 민 • 형사상의 책임은 전적으로 "이용자"가 부담합니다.</li>
				<li>"회사"는 "이용자"가 게시한 내용이 다음 각호에 해당하는 경우 임의로 삭제할 수 있습니다. 다만, "회사"는 이러한 게시물의 삭제의무를 부담하지 않습니다.
					<ol class="list-style04">
						<li><span>1)</span>"회사" 또는 제3자의 지적재산권 등을 침해하는 내용인 경우</li>
						<li><span>2)</span>타인을 비방하거나 명예를 훼손하는 내용인 경우</li>
						<li><span>3)</span>공공질서 및 미풍양속을 해칠 우려가 있는 내용인 경우</li>
						<li><span>4)</span>탈퇴한 회원이 등록한 게시물</li>
						<li><span>5)</span>범죄적 행위에 결부된다고 인정되는 내용일 경우</li>
						<li><span>6)</span>"회사"가 규정한 게시기간을 초과한 경우</li>
						<li><span>7)</span>"회사"의 서비스 제공을 고의적으로 방해하는 내용일 경우</li>
						<li><span>8)</span>기타 이 약관이나 관련법규에 위배된다고 판단되는 경우</li>
					</ol>
				</li>						
				<li>"이용자"는 서비스를 이용하여 얻은 정보를 가공하거나 판매해서는 안되며, 서비스에 게재된 자료를 상업적으로 사용할 수 없습니다.</li>
			</ol>
		</div>
		<h2>제5장 책임과 의무</h2>	
		<div>
			<h3>제14조 (회사의 책임과 의무)</h3>	
			<ol class="list-style03">
				<li>"회사"는 이 약관이 정하는 바에 따라 지속적이고 안정적으로 서비스를 제공하는데 최선을 다하며, 사이트에 장애가 생긴 경우 특별한 사유가 없는 한 지체없이 이를 수리 또는 복구합니다.</li>
				<li>"회사"는 "이용자"의 개인정보를 보호하고 개인정보와 관련한 이용자의 불만을 처리하기 위하여 개인정보보호책임자를 지정하고, 개인정보처리방침을 제정하여 사이트 초기화면에 공시합니다.</li>
				<li>"회사"는 개인정보가 분실, 도난, 유출, 변조 또는 훼손되지 아니하도록 안전성 확보에 필요한 기술적 • 관리적 보호대책을 수립해야 합니다.</li>	
				<li>"회사"는 "이용자"로 부터 정당한 의견이나 불만 제기 등이 있는 경우 즉시 처리하여야 합니다. 다만, 즉시 처리가 곤란한 경우는 "이용자"에게 그 사유와 처리일정을 통보합니다.</li>						
			</ol>
		</div>
		<div>
			<h3>제15조 (이용자의 책임과 의무)</h3>	
			<ol class="list-style03">
				<li>"이용자"는 다음 각 호에 해당하는 행위를 해서는 안됩니다.
					<ol class="list-style04">
						<li><span>1)</span>다른 사람의 명의, 아이디, 비밀번호 등 개인정보를 도용하거나 부정하게 사용하는 행위</li>
						<li><span>2)</span>서비스에서 얻은 정보를 회사의 사전승낙 없이 이용 이외의 목적으로 이용하거나 제3자에게 제공하는 행위</li>
						<li><span>3)</span>"회사"및 제3자의 지적재산권 등을 침해하는 행위</li>
						<li><span>4)</span>공공질서 및 미풍양속에 위반되는 내용의 정보, 문장, 도형 등을 타인에게 유포하는 행위</li>
						<li><span>5)</span>범죄와 결부된다고 객관적으로 판단되는 행위</li>
						<li><span>6)</span>기타 약관 및"회사"가 공지한 사항, 관련법규를 위반하는 행위</li>
					</ol>
				</li>
				<li>"이용자"는 아이디 및 비밀번호 등 본인의 개인정보를 타인이 사용하게 해서는 안되며, 본인의 개인정보를 타인이 이용함으로써 발생한 결과에 대한 책임은 "이용자"에게 있습니다.</li>
				<li>"이용자"는 "회사"의 사전승낙 없이는 서비스를 이용하여 영업활동을 할 수 없으며, 그 영업활동의 결과에 대하여 "회사"는 책임을 지지 않습니다. "이용자"는 이와 같은 영업활동으로 인하여 "회사"에 손해를 발생시킨 경우 그 손해를 배상하여야 합니다.</li>
				<li>"이용자"는 서비스의 이용권한, 기타 이용 계약상 지위를 타인에게 양도, 증여할 수 없으며, 이를 담보로 제공할 수 없습니다.</li>
			</ol>
		</div>
		<div>
			<h3>제16조 (면책)</h3>	
			<ol class="list-style03">
				<li>"회사"는 다음 각 호에 해당하는 경우에는 책임을 지지 않습니다.
					<ol class="list-style04">
						<li><span>1)</span>"이용자"의 아이디 및 비밀번호 등 관리소홀로 손해가 발생할 경우</li>
						<li><span>2)</span>천재지변, 전쟁 등 불가항력적인 사유로 서비스를 제공하지 못하는 경우</li>
						<li><span>3)</span>통신기기, 통신회선 또는 "회사"가 직접 통제할 수 없는 장애에 의한 업무처리 지연이나 불능이 발생한 경우</li>
						<li><span>4)</span>"이용자"가 제15조(이용자의 책임과 의무)를 위반하여 손해가 발생한 경우</li>
						<li><span>5)</span>"이용자"의 동의를 얻어 제휴사에 제공한 정보가 제휴사에 의해 유출이 된 경우</li>
						<li><span>6)</span>기타 "회사"의 고의 또는 과실로 인하여 발생한 사고가 아닌 경우</li>
					</ol>
				</li>
			</ol>
		</div>
		<div>
			<h3>제17조 (지적재산권의 귀속 및 이용제한)</h3>	
			<ol class="list-style03">
				<li>"회사"가 작성한 저작물에 대한 지적재산권은 "회사"에 귀속합니다.</li>
				<li>"이용자"는 사이트를 이용함으로써 얻은 정보 중 "회사"의 지적재산권에 속하는 정보를 "회사"의 사전 승낙 없이 복제, 송신, 출판, 배포, 방송, 기타 방법에 의하여 영리목적으로 이용하거나 제3자에게 제공하여서는 안됩니다.</li>						
			</ol>
		</div>
		<h2>제6장 손해배상 및 분쟁의 해결 등</h2>
		<div>
			<h3>제 18 조 (손해배상)</h3>	
			<ol class="list-style03">
				<li>"회사" 및 "이용자"가 이 약관을 위반하는 경우, 귀책 당사자는 책임을 부담하며, 이로 인하여 상대방에게 손해를 입힌 경우에는 이를 배상하여야 합니다.</li>
				<li>"회사"는 천재지변, 국가비상사태, 전쟁, 파업, 기타" 회사"의 지배범위 밖의 사유로 인하여"이용자"및 제3자에게 발생시킨 손해에 대하여 책임지지 아니합니다.</li>
			</ol>
		</div>
		<div>
			<h3>제19조 (회사와 제휴사와의 관계)</h3>	
			<ol class="list-style03">
				<li>"회사"는 제휴사가 제공하는 서비스에 대하여 보증책임을 지지 않습니다.</li>
				<li>"회사"와 제휴사는 독자적으로 사이트를 운영하므로 "회사"는 제휴사와 "이용자"간에 행해진 거래에 대해 어떠한 책임도 지지 않습니다.</li>
			</ol>
		</div>
		<div>
			<h3>제20조 (통지)</h3>	
			<ol class="list-style03">
				<li>"회사"가 "이용자"에 대하여 개별 통지를 하는 경우," 회사"는 "이용자"가 제공한 주소, 전화번호, 이메일 등 "회사"가 적절하다고 판단하는 방법을 선택할 수 있습니다.</li>
				<li>"회사"가 불특정 다수 "이용자"에 대하여 통지를 하는 경우 "회사"는 1주일 이상 사이트 화면에 게시함으로써 개별 통지에 갈음할 수 있습니다.</li>		
				<li>"이용자“가 잘못된 연락처 정보를 제공하거나 변경된 연락처 정보를 “회사”에 통보하지 않는 등 “이용자”의 귀책 사유 로 인하여 통지를 받지 못하는 경우 "회사"는 책임을 지지 않습니다.</li>						
			</ol>
		</div>
		<div>
			<h3>제21조 (분쟁 해결)</h3>	
			<ol class="list-style03">
				<li>회사"는 "이용자"가 제기하는 정당한 의견이나 불만을 반영하고 그 처리를 위하여 고객상담부서를 설치하여 운영합니다.</li>
				<li>"회사”는 “이용자”로부터 제출되는 불만사항 및 의견을 신속하게 처리합니다. 다만, 신속한 처리가 곤란한 경우에는 이용자에게 그 사유와 처리일정을 통보합니다.</li>		
				<li>"회사"와 "이용자"는 서비스와 관련하여 발생한 분쟁을 원만하게 해결하기 위하여 필요한 모든 노력을 하여야 합니다.</li>						
			</ol>
		</div>
		<div>
			<h3>제22조 (준거법 및 관할법원)</h3>	
			<p class="mt15">"회사"와 "이용자"간에 발생한 분쟁에 대하여는 대한민국 법을 적용하며, 분쟁으로 인한 소송은 민사소송법상 관할을 가지는 대한민국의 법원에 제기합니다.</p>
		</div>
		<div>
			<h3>부칙</h3>	
			<p class="mt15">(시행일) 본 약관은 2016년 8월 1일부터 시행합니다.</p>
			<p class="mt15">이전 버전은 올크레딧 PC Web 이용약관에서 확인하실 수 있습니다.</p>
		</div>
	</div>	
</div>
</template>

<script>
export default {
  name: "Terms3",
};
</script>

